.story-section {
	position: relative;
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 320px;
		transform: translate(-78%, -40%) scaleX(-1);
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 320px;
		transform: translate(78%, -40%);
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		transform: translate(-60%, 40%);
	}
	.story {
		position: relative;
		padding: 5rem 0;
		background: var(--color-primary);
		border-bottom-left-radius: 24px;
		border-bottom-right-radius: 24px;
		@include max-md {
			padding: 3rem 0;
		}
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		text-align: center;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		@include max-md {
			aspect-ratio: 6/4;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.story-background {
				position: relative;
				object-fit: cover;
				width: 100%;
				height: 100%;
			}
		}
	}
}
