.header-section {
	position: relative;
	width: 100%;
	overflow: hidden;
	.scroll-icon {
		position: relative;
		margin-top: 2rem;
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		@include max-md {
			top: 0;
			bottom: unset;
			left: 50%;
			transform: translate(-50%, 10%);
			width: 110%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		left: 0;
		width: 150%;
		transform: translate(-50%, 0);
		opacity: 0.9;
	}
	.orn-3 {
		position: absolute;
		top: 50%;
		right: 0;
		width: 120%;
		transform: translate(65%, -50%) rotate(-5deg);
		opacity: 0.95;
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 150%;
		transform: translate(-50%, 80%);
	}
	.orn-5 {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		transform: translate(-70%, -10%);
	}
	.orn-6 {
		position: absolute;
		top: 0;
		right: 0;
		width: 100%;
		transform: translate(55%, -10%);
		z-index: 2;
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		transform: translate(-58%, 40%);
		z-index: 2;
	}
	.orn-8 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 120%;
		transform: translate(40%, 60%) scaleX(-1);
		z-index: 2;
	}
	.orn-9 {
		position: absolute;
		top: 100%;
		right: 0;
		width: 100%;
		transform: translate(20%, -40%) scaleX(-1);
	}
	.orn-10 {
		position: absolute;
		top: 0;
		right: 0;
		width: 140px;
		transform: translate(-45%, -25%);
	}
	.orn-11 {
		position: absolute;
		top: 0;
		right: 50%;
		width: 120px;
		transform: translate(45%, -35%);
	}
	.header-content {
		position: relative;
		height: 100vh;
		.header {
			position: relative;
			width: 50%;
			height: 100%;
			margin: 0 auto;
			@include max-lg {
				width: 100%;
			}
			.header-title {
				position: absolute;
				padding: calc(25vw - 2rem) 0 0;
				top: 0;
				left: 50%;
				transform: translate(-50%, 0);
				text-align: center;
				@include max-lg {
					padding: calc(50vw - 2rem) 0 0;
				}
				@include max-md {
					padding: calc(50vw + 1rem) 0 0;
				}
				&-content {
					position: relative;
					z-index: 5;
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes zoomInMain {
	0% {
		transform: scaleZ(1);
	}
	100% {
		transform: scale3d(1.1, 1.1, 1.1);
	}
}

@keyframes zoomInMain {
	0% {
		transform: scaleZ(1);
	}
	100% {
		transform: scale3d(1.1, 1.1, 1.1);
	}
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}
