.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				position: absolute;
				top: 50%;
				left: 50%;
				width: 75%;
				aspect-ratio: 3/4;
				object-fit: cover;
				transform: translate(-50%, -50%);
			}
			.frame-photo {
				position: relative;
				width: 100%;
				height: auto;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: relative;
			padding: 3rem 2rem;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
