.venue-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
		.container {
			padding: 0;
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 50%;
		width: 113%;
		transform: translate(-50%, 0);
		z-index: 2;
	}
	.orn-2 {
		position: absolute;
		top: 0;
		left: 30%;
		width: 80px;
		transform: translateX(-60%);
	}
	.orn-3 {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		transform: translate(-60%, 0);
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100%;
		transform: translate(60%, 0);
	}
	.orn-5 {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		transform: translate(-75%, -10%) scaleX(-1);
	}
	.orn-6 {
		position: absolute;
		top: 1rem;
		right: 0;
		width: 260px;
		transform: translate(65%, 0);
	}
	.orn-7 {
		position: absolute;
		top: 60%;
		left: 0;
		width: 240px;
		transform: translate(-68%, -50%) scaleX(-1);
		z-index: 6;
	}
	.orn-8 {
		position: absolute;
		top: 100%;
		right: 0;
		width: 240px;
		transform: translate(15%, -35%) scaleX(-1);
	}
	.orn-9 {
		position: absolute;
		top: 65%;
		right: 0;
		width: 240px;
		transform: translate(70%, -50%) scaleX(-1);
		z-index: 6;
		pointer-events: none;
	}
	.orn-10 {
		position: absolute;
		top: 60%;
		left: 0;
		width: 240px;
		transform: translate(0, -50%) scaleX(-1);
	}
	.orn-11 {
		position: absolute;
		top: 0;
		right: 0;
		width: 60px;
		transform: translate(-10%, 0);
	}
	.venue-wrapper {
		position: relative;
		z-index: 3;
		.venue-description {
			position: relative;
			margin: 0 auto 5rem;
			z-index: 2;
			text-align: center;
			@include max-lg {
				margin-bottom: 3rem;
			}
			@include max-md {
				padding: 0 1.5rem;
			}
		}
		.venue-content {
			position: relative;
			.ribbon-venue {
				position: relative;
				svg {
					width: 40px;
					height: auto;
					path {
						stroke: var(--color-dark);
					}
				}
			}
			.card {
				background-color: transparent;
				height: 100%;
				z-index: 5;
				text-align: center;
				overflow: hidden;
				border: none;
				.card-body {
					position: relative;
					padding: calc(30vh + 1rem) 2rem 3rem;
					@include max-lg {
						padding: calc(20vw) 1rem 2rem;
					}
					@include max-md {
						padding: calc(35vw + 5rem) 0 2rem;
					}
					h4 {
						margin: 1rem 0;
					}
					hr {
						display: block;
						width: 10%;
						height: 2px;
						margin: 0 auto 1rem;
					}
				}
			}
		}
	}
	.prokes-wrapper {
		margin: 3rem 0 0;
		position: relative;
		padding: 2rem;
		text-align: center;
		z-index: 2;
		.title-section {
			&::before {
				all: unset;
			}
		}
		@include max-md {
			padding: 2rem 0;
		}
		* {
			position: relative;
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 45%;
			}
		}
	}
	.row-venue {
		@include max-md {
			gap: 3rem;
		}
	}
	.event-name {
		position: relative;
		width: 100%;
		padding: 0 calc(6vw - 1.5rem) 2rem;
		overflow: hidden;
		.date {
			position: relative;
			width: 100%;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			h5 {
				font-family: $font-secondary;
				font-weight: 700;
				margin-bottom: 0;
				font-size: 48px;
				min-width: 30%;
				@include max-lg {
					font-size: 36px;
				}
			}
		}
		h4 {
			font-family: $font-secondary;
		}
		p {
			margin-bottom: 0;
			font-weight: 600;
		}
	}
	.event-place {
		position: relative;
		background: var(--color-primary);
		border-radius: var(--border-radius);
		padding: 2rem 2rem 0;
		z-index: 5;
		.btn-custom {
			transform: translate(0, 45%);
		}
	}
	.animation-loop {
		.st1 {
			will-change: transform;
			animation: zoomInRing 2000ms ease-out infinite;
		}
		.dot {
			will-change: transform;
			animation: fadeBoing 2000ms ease-out infinite;
			transform-origin: 50% 75%;
		}
		#Layer_3 {
			will-change: transform;
			animation: boing 1000ms ease-out infinite alternate;
		}
	}

	@keyframes zoomInRing {
		0% {
			opacity: 0;
			transform: scale3d(0.5, 0.5, 0.5);
		}
		40% {
			opacity: 1;
		}
		70% {
			opacity: 0;
			transform: scale3d(1.25, 1.25, 1.25);
		}
		100% {
			opacity: 0;
		}
	}

	@keyframes boing {
		0% {
			transform: translate3d(0, 0, 0);
		}
		100% {
			transform: translate3d(0, -5rem, 0);
		}
	}

	@keyframes fadeBoing {
		0% {
			opacity: 1;
		}
		50% {
			transform: scale3d(0.25, 0.25, 0.25);
			opacity: 0;
		}
		100% {
			transform: scale3d(1, 1, 1);
			opacity: 1;
		}
	}
}

.maps-embed {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
