.couple-section {
	position: relative;
	text-align: center;
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		margin-bottom: 1rem;
		z-index: 3;
		span {
			position: relative;
			font-size: 48px;
			font-family: $font-secondary;
		}
	}
	.couple-wrapper {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
		.orn-1 {
			position: absolute;
			top: 1rem;
			left: 0;
			width: 320px;
			transform: translate(-80%, 0) scaleX(-1);
		}
		.orn-2 {
			position: absolute;
			top: 5rem;
			right: 0;
			width: 260px;
			transform: translate(75%, 0) scaleX(-1);
		}
		.orn-3 {
			position: absolute;
			top: 15%;
			right: 0;
			width: 100%;
			transform: translate(60%, 0);
		}
		.orn-4 {
			position: absolute;
			top: 50%;
			left: 0;
			transform: translate(-65%, -50%) scaleX(-1);
		}
		.orn-5 {
			position: absolute;
			bottom: 10%;
			right: 0;
			width: 100%;
			transform: translate(60%, 0);
		}
		.orn-6 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			transform: translate(-60%, 0);
		}
	}
	.couple-body {
		position: relative;
		padding: 5rem 1rem;
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background: var(--color-primary);
			opacity: 0.6;
			border-radius: 12px;
			content: "";
		}
	}
	.couple-content {
		position: relative;
	}
	.couple {
		position: relative;
		text-align: center;
		padding: 1rem;
		@include max-md {
			padding: 0;
		}
		.image-wrap {
			width: 100%;
			position: relative;
			margin: 0 auto;
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				.couple-image {
					position: absolute;
					width: 75%;
					height: auto;
					aspect-ratio: 3/4;
					object-fit: cover;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
				}
				.frame-photo {
					position: relative;
					width: 100%;
					height: auto;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 1rem;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.4rem 0.48rem;
			border-radius: 50px;
			margin: 0 auto;
			.sosmed {
				color: var(--color-light);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
