.rsvp-section {
	position: relative;
	padding: 0 0 5rem;
	@include max-md {
		padding: 3rem 0;
		.container {
			padding: 0;
		}
	}
	.orn-1 {
		position: relative;
		width: 100%;
		pointer-events: none;
		@include max-md {
			left: 50%;
			transform: translateX(-50%);
			width: 125%;
		}
	}
	.rsvp-wrapper {
		position: relative;
		.rsvp-form {
			position: absolute;
			top: 50%;
			left: 50%;
			width: 75%;
			transform: translate(-50%, -50%);
			background: var(--color-primary);
			padding: 5rem 2rem;
			@include max-md {
				width: 90%;
			}
			.title-section {
				&::before {
					all: unset;
				}
			}
			.form-control,
			.form-select {
				background-color: rgba($color: #fff, $alpha: 0.7);
			}
		}
	}
}
