.reminder-wrap {
	position: relative;
	display: block;
	padding: calc(40vh + 1rem) 0;
	background: var(--color-primary);
	@include max-md {
		padding: calc(35vh) 0;
	}
	.orn-1 {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 170%;
		transform: translate(-50%, -50%);
	}
	.orn-2 {
		position: absolute;
		top: 3rem;
		right: 0;
		width: 100%;
		transform: translate(50%, 50%);
	}
	.orn-3 {
		position: absolute;
		bottom: 3rem;
		left: 0;
		width: 100%;
		transform: translate(-50%, -20%);
	}
	.orn-4 {
		position: absolute;
		top: 3rem;
		left: 0;
		width: 240px;
		transform: translate(-30%, 0) scaleX(-1);
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100%;
		transform: translate(65%, 0);
	}
	.reminder-content {
		position: relative;
		width: fit-content;
		max-width: 100%;
		margin: 0 auto;
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1.5rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
